import React from 'react'
import { Link } from 'gatsby'

import logo from '../img/logo.svg'
import facebook from '../img/social/facebook.svg'
import instagram from '../img/social/instagram.svg'
import twitter from '../img/social/twitter.svg'
import vimeo from '../img/social/vimeo.svg'

const Footer = class extends React.Component {
  render() {
    return (
      <footer className="footer has-background-black has-text-white-ter">
        <div className="content has-text-centered">
                <div className="container">
                    <hr></hr>
                    <nav className="row justify-content-between align-items-center">
                        <div className="col-auto">
                            <ul className="list-inline">
                                <li className="list-inline-item">
                                    <a href="#">
                                        <img alt="Image" src="/cellardoor.png" />
                                    </a>
                                </li>
                                <li className="list-inline-item">
                                    <a href="/">Home</a>
                                </li>
                                <li className="list-inline-item">
                                    <a href="/contact">Contact Us</a>
                                </li>
                                <li className="list-inline-item">
                                    <a href="#">Terms &amp; Conditions</a>
                                </li>
                            </ul>
                        </div>
                        <div className="col-auto text-sm-right">
                            <ul className="list-inline">
                                <li className="list-inline-item">
                                    <a href="#"><i className="socicon-twitter"></i></a>
                                </li>
                                <li className="list-inline-item">
                                    <a href="#"><i className="socicon-facebook"></i></a>
                                </li>
                            </ul>
                        </div>
                    </nav>
                    <div className="row">
                        <div className="col">
                            <small>&copy; 2018 NZ Cellar Door</small>
                        </div>
                    </div>
                </div>
			</div>
      </footer>
    )
  }
}

export default Footer
